<template>
  <IntExtLink
    :class="{ hidden: !show }"
    class="scroll-top is-hidden-desktop"
    :to="'#app'"
    title="Back to top"
  >
    <img
      class="scroll-top-icon"
      :src="$getAsset('up.svg')"
      alt="Back to top"
    >
  </IntExtLink>
</template>

<script>
import Vue from 'vue'

export default Vue.extend({
  data () {
    return {
      show: false
    }
  },

  mounted () {
    const onScroll = () => {
      const newValue = window.scrollY > window.innerHeight * 0.3

      if (newValue === this.show) { return }

      this.show = newValue
    }

    onScroll()

    window.addEventListener('scroll', onScroll)
    this.$once('hook:beforeDestroy', () => {
      window.removeEventListener('scroll', onScroll)
    })
  }
})
</script>
