/// <reference path="./shims.d.ts" />
import Dropdown from 'buefy/src/components/dropdown'
import Modal from 'buefy/src/components/modal'
import Tabs from 'buefy/src/components/tabs'
import Vue from 'vue'
import VueMeta from 'vue-meta'

import App from './App.vue'
import components from './components'

Vue.config.productionTip = false

for (const [name, component] of Object.entries(components)) {
  Vue.component(name, component)
}

const packageInfo = {
  version: process.env.VUE_APP_INTERACTJS_VERSION
}

Vue.mixin({
  beforeCreate () {
    (Vue as any).util.defineReactive(this, '$packageInfo', packageInfo)
  },
  methods: {
    $getAsset: path => require('@/assets/' + path)
  }
})

Vue.prototype.__prerendering = !!(window as any).__PRERENDER_INJECTED
Vue.prototype.__prerendered = !!document.querySelector('#app[data-server-rendered]')
Vue.use(VueMeta, {
  refreshOnceOnNavigation: true
})

for (const componentPlugin of [Dropdown, Modal, Tabs]) {
  Vue.use(componentPlugin)
}

;(async () => {
  const router = (await import(/* webpackChunkName: "router" */ './router')).default

  new Vue({
    router,
    render: h => h(App)
  }).$mount('#app')
})()
