<template>
  <keep-alive
    :key="$route.path"
    :max="2"
  >
    <div
      id="app"
      class="app-wrapper"
      :data-page-loaded="pageLoaded"
      :data-server-rendered="__prerendering"
    >
      <div id="top" />
      <router-view />
    </div>
  </keep-alive>
</template>

<script type="ts">
import { pageLoaded } from './utils'

export default {
  metaInfo: {
    title: 'interact.js',
    titleTemplate: '%s | interact.js'
  },
  data () {
    return {
      pageLoaded: false
    }
  },
  mounted () {
    this.$nextTick(() => document.dispatchEvent(new Event('APP_MOUNTED')))

    if (!this.__prerendering) {
      pageLoaded.then(() => { this.pageLoaded = true })
    }
  }
}
</script>

<style lang="sass" src="./sass/main.sass"/>
