import config from './config'

export function isInView (element) {
  if (document.hidden) { return true }

  const rect = element.getClientRects()[0]

  if (!rect) { return false }

  const { top, height } = rect
  const middle = top + height / 2
  const margin = Math.max(height / 2, window.innerHeight / 4, 200)

  if (Math.abs(middle - window.innerHeight / 2) < margin) {
    return true
  }

  return false
}

export async function whenScrolledInView (element) {
  return new Promise(resolve => {
    if (isInView(element)) {
      resolve(null)
      return
    }

    window.addEventListener('scroll', onScroll)
    window.addEventListener('resize', onScroll)

    function onScroll () {
      if (isInView(element)) {
        window.removeEventListener('scroll', onScroll)
        window.removeEventListener('resize', onScroll)
        resolve(null)
      }
    }
  })
}

export function timeout (duration) {
  return new Promise(resolve => setTimeout(resolve, duration))
}

export function getMdVue (module, data?) {
  if (module.then) {
    return module.then(m => getMdVue(m.default || m))
  }

  const { vue, attributes } = module

  vue.component.metaInfo = () => attributes
  vue.component.data = () => ({ ...attributes, ...data })

  return vue.component
}

export const pageLoaded = new Promise(resolve => {
  if (document.readyState === 'complete') {
    resolve(true)
    return
  }

  window.addEventListener('load', () => resolve(true))
})

export function apiRequest (endpoint, data, options) {
  options = data
    ? {
      method: 'POST',
      body: data && JSON.stringify(data),
      ...options,
      headers: { 'Content-Type': 'application/json', ...(options || {}).headers }
    }
    : options

  return fetch(`https://api.${config.domain}/${endpoint}`, options).then(r => r.json())
}

let packageVersion = ''

export async function getVersion () {
  if (!packageVersion) {
    packageVersion = await apiRequest('version').catch(() => '')
  }

  return packageVersion
}
