<template>
  <a
    v-show="!version"
    class="version-badge tags has-addons is-inline-flex"
    :href="'https://www.npmjs.com/package/interactjs/v/' + version"
  >
    <span class="tag is-light">version</span>
    <span :class="['tag', /-(beta|alpha|rc)/.test(version) ? 'is-warning' : 'is-success']">
      {{ version }}
    </span>
  </a>
</template>

<style lang="sass">
.version-badge
  transition: opacity 0.25s
</style>

<script>
import Vue from 'vue'

import { getVersion } from '../utils'

export default Vue.extend({
  data () {
    return { version: '' }
  },
  async created () {
    this.version = await getVersion()
  }
})
</script>
