<template>
  <div
    style="max-width: 20em; margin: auto"
    class="has-text-centered"
  >
    <div
      v-if="success"
      class="notification is-primary"
    >
      Thanks! Check your inbox for a confirmation email.
    </div>
    <div
      v-else
      class="delay-point-5"
    >
      <div>
        <form
          class="box is-shadowless newsletter-form has-bg-primary-dark"
          @submit.prevent="requestSubscribe()"
        >
          <div class="has-text-light">
            <slot>
              Subscribe to our spam-free newsletter to stay up to date
              with new features and releases
            </slot>
          </div>
          <br>
          <div class="field">
            <label
              :for="nameId"
              class="label is-small has-text-left has-text-light"
            >First Name</label>
            <div class="control">
              <input
                :id="nameId"
                v-model.trim="name"
                class="input is-small"
                placeholder="Name"
                required="true"
              >
            </div>
          </div>

          <div class="field">
            <label
              :for="emailId"
              class="label is-small has-text-left has-text-light"
            >Email address</label>
            <div class="control">
              <input
                :id="emailId"
                v-model.trim="email"
                class="input is-small"
                placeholder="mail@example.com"
                type="email"
                required="true"
              >
            </div>
          </div>

          <div class="field is-grouped is-grouped-right">
            <div class="control">
              <button
                type="submit"
                class="button is-primary is-small"
              >
                Subscribe
              </button>
            </div>
          </div>
        </form>

        <template v-if="error">
          <div class="notification is-warning has-text-centered">
            <span v-if="!error.type || error.type === 'ServerError'">
              Something went wrong. Please try to subscribe again later.
            </span>
            <span v-else>
              {{ error.details.join(' ') }}
            </span>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'

import config from '../config'

export default Vue.extend({
  data () {
    return {
      name: '',
      email: '',
      nameId: `name-${this._uid}`,
      emailId: `email-${this._uid}`,
      success: false,
      error: false
    }
  },
  methods: {
    requestSubscribe () {
      const { email, name } = this

      this.error = null

      fetch(`https://api.${config.domain}/newsletter/subscribe`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ email, name })
      })
        .then(response => response.json())
        .then(response => {
          const { success, error } = response

          if (error) { throw error }

          this.success = success
        })
        .catch((error) => {
          this.error = error
        })
    }
  }
})
</script>
